import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';
import injectSheet from 'react-jss';

import {
    NamespacesConsumer
} from 'react-i18next';

import {
    Row,
    Col,
    Grid
} from 'react-flexbox-grid';

import SkillBar from './SkillBar';

import skillsStyle from '../assets/jss/components/skillsStyle';

class Skills extends React.Component {

    render() {
        const {
            classes,
            className
        } = this.props;

        return (
            <NamespacesConsumer>
                {(t, { i18n }) =>
                    <div className={classNames(classes.className, classes.container)}>
                        <div className={classes.curlyBrace}>
                            <div className={classNames(classes.brace, classes.leftBrace)} />
                            <div className={classNames(classes.brace, classes.rightBrace)} />
                        </div>
                        <div className={classes.skills}>
                            <SkillBar className={classes.skillBar} label="HTML" proficiency={97} color="html" />
                            <SkillBar className={classes.skillBar} label="CSS" proficiency={84} color="css" />
                            <SkillBar className={classes.skillBar} label="Javascript" proficiency={92} color="js" />
                            <SkillBar className={classes.skillBar} label="React" proficiency={87} color="react" />
                            <SkillBar className={classes.skillBar} label="PHP" proficiency={90} color="php" />
                            <SkillBar className={classes.skillBar} label="Ionic" proficiency={81} color="ionic" />
                        </div>
                    </div>
                }
            </NamespacesConsumer>
        );
    }
}

Skills.propTypes = {
    className: PropTypes.string,
    classes: PropTypes.object.isRequired
};

export default injectSheet(skillsStyle)(Skills);
